import React from 'react';
/** @jsx jsx */
import { css, jsx, Global } from '@emotion/core';
import styled from "@emotion/styled";
import pic from '../images/img.jpg'

const apikey = process.env.GATSBY_OMEKA_API_KEY
const apiurl = 'https://cors-anywhere.herokuapp.com/http://publications.newberry.org/transcription/mms-transcribe/api/items?key=' + apikey

let breakPoints = [350, 500, 750];

const Masonrycontainer = styled.div`
    width: 80%;
    margin: auto;
    margin-top: 10px;
    text-align: center;
    padding: 4px;
    color: #efefef;
`
const Masonrycss = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-content: stretch;
    width: 100%;
    margin: auto;
`
const Column = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-content: stretch;
    flex-basis: 220px;
`
class Masonry extends React.Component {
    constructor(props) {
        super(props);
        this.state = { columns: 1 };
        this.onResize = this.onResize.bind(this);
    }
    componentDidMount() {
        this.onResize();
        window.addEventListener('resize', this.onResize);
    }
    componentWillUnmount() {
        window.removeEventListener('resize', this.onResize);
    }
    getColumns(w) {
        return breakPoints.reduceRight((p, c, i) => {
            return c < w ? p : i;
        }, breakPoints.length) + 1;
    }
    onResize() {
        const columns = this.getColumns(this.refs.Masonry.offsetWidth);
        if (columns !== this.state.columns) {
            this.setState({ columns: columns });
        }
    }
    mapChildren() {
        let col = [];
        const numC = this.state.columns;
        for (let i = 0; i < numC; i++) {
            col.push([]);
        }
        return this.props.children.reduce((p, c, i) => {
            p[i % numC].push(c);
            return p;
        }, col);
    }
    render() {
        return (
            <Masonrycss className="masonry" ref="Masonry">
                {this.mapChildren().map((col, ci) => 
                    <Column className="column" key={ci}>
                        {col.map((child, i) => 
                            <div key={i}>{child}</div>
                        )}              
                    </Column>
                )}
            </Masonrycss>
        )
    }
}


const Tile = ({ title, id }) => 
    <div className="tile" css={css`
            background: rgba(255,255,255,0.5);
        background: rgba(255,255,255},0.85);
            background-attachment: fixed;
            background-position: center;
        color: black;
        margin: 4px;
        border: 2px solid #27452B;
        flex-basis: 150px;
        border-radius: 6px;
        padding: 10px 10px 25px 10px;
        position: relative;
        transition: all .15s ease-in-out;
        & p {
            padding: 5px 10px 15px 10px;
        }
        &:before {
            content: "";
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            z-index: -1;
            -webkit-box-shadow: 10px 10px 50px 0px rgba(0,0,0,0.75);
            -moz-box-shadow: 10px 10px 50px 0px rgba(0,0,0,0.75);
            box-shadow: 10px 10px 50px 0px rgba(0,0,0,0.75);
        }
        &:hover {
            background: white;
            background-attachment: fixed;
            background-position: center;
        }
    `}>
    <img css={css`
            padding: 25px 25px 5px 25px;
            width: 150px;

            filter: drop-shadow(0 0 0.75rem black);
        `} 
        src={pic} alt="person"/>
    <h2 css={css`font-family: 'Hepto Slab', serif;`}>{title}</h2>
    <p css={css`font-family: 'Lato', sans-serif;`}>{title.length}</p>

</div>
export default class Vmasonry extends React.Component {
    constructor() {
        super()
        this.handleClick = this.handleClick.bind(this)
        this.state = {
            colorState: false,
            collections: []
        }
    }
    componentDidMount() {
        fetch(apiurl)
        .then(res => res.json())
        .then((data) => {
            this.setState({ collections: data })
        })
        .catch(console.log)
    }
    handleClick() {
        this.setState(prevState => ({
            colorState: !prevState.colorState
        }));
    }
    render() {
        const colors = this.state.colorState ? {
            dark: '#27452B',
            darkrgba: '39,69,43', 
            light: '#D6D7B5',
            lightrgba: '214,215,181'
        } : {
            dark: '#000',
            darkrgba: '0,0,0', 
            light: '#fff',
            lightrgba: '255,255,255'
        }
        return ( <div>
            <div className="container" css={css`
                position: fixed;
                overflow: hidden;
                left: 0;
                right: 0;
                top: 0;
                bottom: 0;
                z-index: -1;
                
            `}></div>
                <Masonrycontainer>
                        <Masonry breakPoints={breakPoints}>
                        {this.state.collections
                            .slice(0, 10)
                            .map((coll, i) => <Tile className="twistessa" key={i} title={coll.element_texts[0].text} id={coll.id} />)}
                        </Masonry>
                </Masonrycontainer>
            </div>
        )
    }
}